'use client';
import React, { useEffect } from 'react';

export const Header = () => {
  useEffect(() => {
    if (typeof window !== 'undefined') {
      require('bootstrap/dist/js/bootstrap.bundle.min.js');
    }
  }, []);
  return (
    <div
      className="d-flex justify-content-between align-items-center px-3 px-md-5 py-3"
      style={{ background: '#181818', borderBottom: '1px solid #bfa046', boxShadow: '0 2px 12px rgba(0,0,0,0.08)' }}
    >
      <div className="d-flex align-items-center gap-3">
        <img
          style={{ width: '48px', height: '48px', borderRadius: '50%', border: '2.5px solid #bfa046', objectFit: 'cover', boxShadow: '0 2px 8px rgba(191,160,70,0.12)' }}
          src="https://images.scalebranding.com/elegant-b-letter-logo-e7491532-e662-48ce-ad79-29c55306d662.jpg"
          alt="Logo"
        />
        <span className="fw-bold fs-4" style={{ color: '#fff', fontFamily: 'Playfair Display, serif', letterSpacing: '1.5px', textShadow: '0 1px 4px #0002' }}>BrandName</span>
      </div>
      <div className="d-flex align-items-center gap-2">
        <button className="btn rounded-circle p-2 border-0" title="Toggle Theme" type="button" style={{ background: 'rgba(255,255,255,0.08)', color: '#bfa046' }}>
          <span role="img" aria-label="theme" style={{ fontSize: '1.2rem' }}>ðŸŒ“</span>
        </button>
        <button className="btn rounded-circle p-2 border-0 position-relative" title="Notifications" type="button" style={{ background: 'rgba(255,255,255,0.08)', color: '#bfa046' }}>
          <span role="img" aria-label="notifications" style={{ fontSize: '1.2rem' }}>ðŸ””</span>
          <span className="position-absolute top-0 start-100 translate-middle badge rounded-pill" style={{ background: '#bfa046', color: '#181818', fontSize: '0.7rem' }}>3</span>
        </button>
        <button className="btn rounded-pill px-3 py-1 fw-semibold" style={{ background: 'transparent', border: '1.5px solid #bfa046', color: '#fff' }}>Login</button>
        <button className="btn rounded-pill px-3 py-1 fw-semibold" style={{ background: '#bfa046', color: '#181818', border: 'none' }}>Register</button>
        <button className="btn rounded-pill px-3 py-1 fw-semibold" style={{ background: 'transparent', border: '1.5px solid #bfa046', color: '#bfa046' }}>Feedback</button>
      </div>
    </div>
  );
};

export default Header;
